// Schema mais robusto para validar os dados da API
const leadSchema = z.object({
  id: z.number(),
  nome: z.string().nullable().optional().default("Sem nome"),
  email: z.string().nullable().optional(),
  whatsapp: z.string().nullable().optional().default("Não informado"), // TORNOU-SE OPCIONAL
  sexo: z.string().nullable().optional(),
  dataNascimento: z.string().nullable().optional(),
  membroDesde: z.string().nullable().optional(),
  voluntario: z.boolean().nullable().optional(),
  voluntarioDesde: z.string().nullable().optional(),
  ministerio: z.array(z.string()).nullable().optional(),
  camiseta: z.string().nullable().optional(),
  batizado: z.preprocess((val) => {
    if (val === null || val === undefined) return false;
    const s = String(val).toLowerCase();
    return s === 'true' || s === 'sim' || s === '1';
  }, z.boolean()),
  batizadoDesde: z.string().nullable().optional(),
  createdAt: z.string(),
});

type Lead = z.infer<typeof leadSchema>;

// Hook customizado para buscar e gerenciar os dados dos leads
function useLeadsData() {
  const [data, setData] = React.useState<Lead[]>([]);
  const [loading, setLoading] = React.useState(true);

  const fetchData = React.useCallback(() => {
    setLoading(true);
    fetch("/api/leads")
      .then((res) => {
        if (!res.ok) throw new Error("Falha ao buscar os dados");
        return res.json();
      })
      .then((json) => {
        // Linha de diagnóstico para te ajudar a ver os dados brutos
        console.log("DADOS RECEBIDOS DA API:", json); 
        
        const validatedData = z.array(leadSchema).parse(json);
        setData(validatedData);
      })
      .catch((error) => {
        console.error("Erro ao processar leads:", error);
        toast.error("Não foi possível carregar os leads.");
        setData([]);
      })
      .finally(() => setLoading(false));
  }, []);

  React.useEffect(() => {
    fetchData();
  }, [fetchData]);

  return { data, loading, refetch: fetchData };
}

